.main-slider__wrap {
  position: relative;
  margin: 0 auto;
  width: 1017px;
  height: 485px; }

.main-slider__input {
  display: none; }

.main-slider__pic {
  position: relative;
  width: inherit;
  height: inherit; }

.main-slider__arrow label {
  margin-top: 20px;
  width: 35px;
  height: 65px;
  position: absolute;
  visibility: hidden;
  background-color: #a292b3;
  border: none;
  outline: none;
  cursor: pointer;
  z-index: 2; }
  .main-slider__arrow label:hover {
    opacity: 0.7; }

.main-slider__arrow--right label {
  top: 200px;
  right: 0; }

.main-slider__arrow--left label {
  top: 200px;
  left: 0; }

.main-slider__ico {
  margin-top: 17px;
  margin-left: 7px; }

.main-slider__img {
  position: absolute;
  width: inherit;
  height: inherit;
  opacity: 0;
  z-index: 0; }

.main-slider__img--img1 {
  background: url("../img/logo-slider.png") no-repeat 50% 50%, url("../img/slider-1.jpg"), blue; }

.main-slider__img--img2 {
  background: url("../img/logo-slider.png") no-repeat 50% 50%, red; }

.main-slider__img--img3 {
  background: url("../img/logo-slider.png") no-repeat 50% 50%, green; }

.main-slider__input--img1:checked ~ .main-slider__arrow--right label[for="img-2"],
.main-slider__input--img2:checked ~ .main-slider__arrow--right label[for="img-3"],
.main-slider__input--img3:checked ~ .main-slider__arrow--right label[for="img-1"],
.main-slider__input--img1:checked ~ .main-slider__arrow--left label[for="img-3"],
.main-slider__input--img2:checked ~ .main-slider__arrow--left label[for="img-1"],
.main-slider__input--img3:checked ~ .main-slider__arrow--left label[for="img-2"] {
  visibility: visible; }

.main-slider__input--img1:checked ~ .main-slider__pic .main-slider__img--img1,
.main-slider__input--img2:checked ~ .main-slider__pic .main-slider__img--img2,
.main-slider__input--img3:checked ~ .main-slider__pic .main-slider__img--img3 {
  opacity: 1;
  z-index: 1; }

.main-slider__paragraph {
